@import "../../assets/scss/variables.scss";

.orders-page {
  & .filters {
    display: flex;
    justify-content: space-between;

    &__list {
      display: flex;
      flex-wrap: wrap;
      column-gap: 15px;
      row-gap: 10px;
      margin-right: 15px;
    }
  }

  & .orders {
    height: 267px;
    overflow: auto;

    &::-webkit-scrollbar {
      width: 8px;
      background-color: $white;
      border-left: 1px solid $gray-light;
    }

    &::-webkit-scrollbar-thumb {
      width: 2px;
      background-color: $gray;
      border-radius: 4px;
      border: 2px solid transparent;
      background-clip: content-box;
    }

    &__item {
      display: flex;
      flex-wrap: nowrap;
      justify-content: space-around;
      align-items: center;

      &:hover {
        background-color: $gray-light;
      }

      & > .car-image {
        max-height: 63px;
        width: fit-content;
      }
    }
  }
}
